import React, {useState} from 'react'
import { Row, Col, Card, Form, DatePicker, Select, Input } from 'antd';

const { Option } = Select;

const rules = {
	departure: [
		{
			required: true,
			message: 'Select departure location',
		}
	],
	destination: [
		{
			required: true,
			message: 'Select destination location'
		}
	],
	item: [
		{
			required: true,
			message: 'Please enter item name',
		}
	],
	client: [
		{
			required: true,
			message: 'Client name is required',
		}
	],
	date: [
		{
			required: true,
			message: 'Starting date is required',
		}
	],
}

const GeneralField = props => {
	const {destinations, destinationsLoading} = props;


	return (
		
	<Row gutter={16} className='justify-content-center'>
		
		<Col xs={24} sm={24} md={17}>
			<Card>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="client" label="Client name" rules={rules.client}>
							<Input className="w-100"/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="item" label="Item to ship" rules={rules.item}>
							<Input className="w-100"/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8}>
						<Form.Item name="date" label="Starting date" rules={rules.date}>
							<DatePicker className="w-100" format="DD-MM-YYYY"/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8}>
						<Form.Item name="departure" label="Departure Address" rules={rules.departure}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select departure"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8}>
						<Form.Item name="destination" label="Destination Address" rules={rules.destination}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select destination"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
			</Card>
		</Col>
	</Row>
)}

export default GeneralField
